@mixin fontSize($sizeValue){
    font-size: $sizeValue * 1px;
    font-size: ($sizeValue/10) * 1rem;
  }

// Header SVG 
.text {
  fill: none;
  stroke-width: 2;
  stroke-linejoin: round;
  stroke-dasharray: 700 230;
  stroke-dashoffset: 0;
  -webkit-animation: stroke 10s ease;
  animation: stroke 10s ease;
}

.text:nth-child(5n + 1) {
  stroke: white;
  -webkit-animation-delay: -1.6s;
  animation-delay: -1.6s;
}
