@-webkit-keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}

/* Other styles */
html, body {
  height: 100%;
}

.headtitle {
  background-size: .2em 100%;
  @include fontSize(35);
  text-transform: uppercase;
  margin-bottom: 40px;
}

svg {
  width: 100%;
  height: 100%;
  position: relative;
  top: 50px;
  }

